@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600);
body {
  font-family: "Open Sans", sans-serif;
  font-weight: 400; }

.container {
  margin: 0 auto;
  width: 50rem; }

h1, h2, h3, h4, h5 {
  font-weight: 600; }

h1 {
  font-size: 1.8rem; }

h2 {
  font-size: 1.6rem; }

h3 {
  font-size: 1.4rem; }

h4 {
  font-size: 1.2rem; }

h5 {
  font-size: 1rem; }

nav {
  background: #1abc9c;
  padding: 1rem; }

nav div.container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between; }

nav a {
  color: white;
  text-decoration: none; }

nav ul li {
  display: inline; }

nav ul li a {
  color: white;
  padding: 0 1rem;
  text-decoration: none; }

